<style lang="less" scoped>
  .demo-form-inline {
    margin: 10px;
    height: 32px;
  }
</style>

<template>
  <div>
    <el-table
      :data="storeList"
      size="medium"
      style="width: 100%">

      <el-table-column type="index">
      </el-table-column>
      <el-table-column
        prop="store"
        label="仓库"
        min-width="80"
        sortable>
        <el-input slot-scope="{row}"
                  placeholder="name"
                  v-model="row.newUser"
                  size="small"
                  clearable>
        </el-input>
      </el-table-column>
      <el-table-column
        label="proxyHost"
        sortable
        prop="proxyHost"
        min-width="90">
        <el-input slot-scope="{row}"
                  placeholder="请输入代理服务地址"
                  v-model="row.proxy.host"
                  size="small"

                  clearable>
        </el-input>

      </el-table-column>
      <el-table-column
        label="端口"
        sortable
        prop="port"
        min-width="90">
        <el-input slot-scope="{row}"
                  placeholder="请输入代理端口号"
                  v-model="row.proxy.port"
                  size="small"
                  clearable>
        </el-input>

      </el-table-column>
      <el-table-column label="cache" min-width="70">
        <el-switch slot-scope="{row}"
                   v-model="row.proxy.cache"
                   active-color="#13ce66"

        >
        </el-switch>
      </el-table-column>
      <el-table-column label="运行状态" min-width="70">
        <el-switch slot-scope="{row}"
                   v-model="row.proxy.start"
                   active-color="#13ce66"

        >
        </el-switch>
      </el-table-column>
      <el-table-column label="提交" min-width="60">
        <el-button slot-scope="{row}"
                   size="mini"
                   type="primary"
                   @click="submit(row)"
                   :disabled="loading"
        >
          提交
        </el-button>
      </el-table-column>
      <el-table-column label="删除" min-width="60">
        <el-button v-if="row.store != 'default'" slot-scope="{row}"
                   size="mini"
                   type="danger"
                   :disabled="loading"
                   @click="deleteStore(row)"
        >
          删除
        </el-button>
      </el-table-column>
    </el-table>


  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import mixin from './mixin'

  export default {
    data() {
      return {
        loading: false,
      };
    },
    computed: {
      ...mapState(['storeList']),
    },
    created() {

    },
    mixins: [mixin],
    methods: {
      submit(storeObj) {
        if (!this.test(storeObj)) return;
        this.loading = true
        this.post('/setting/updateStore', storeObj).then(json => {
          this.tip('修改成功！')
          this.dispatch('getStoreList')
        }).finally(() => {
          this.loading = false
        })
      },
      deleteStore(storeObj) {
        var params = {user: storeObj.user}
        if (!this.test(params)) {
          return;
        }
        this.$confirm('此操作将永久删除该仓库, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(_ => {
          this.loading = true
          this.post('/setting/rmStore', params).then(json => {
            this.tip('删除成功')
            this.dispatch('getStoreList')
          })
        }).finally(() => {
          this.loading = false
        })
      }
    }
  }

</script>



